import React from "react";
import { planlector } from "../../data/telescopeData";
import withReactContent from "sweetalert2-react-content";
import Swal from "sweetalert2";

const PlanLector = () => {
    const Swaled = withReactContent(Swal)

    const openPost = (post) => {
        Swaled.fire({
            title: post.autor,
            background: '#649DC1',
            html:
            <div>
                {post.embeds.map((embed) => {
                    return(
                        <div>
                            {embed}
                        </div>
                    )
                })}
                {post.planFotos.map((image) => {
                    return(
                        <img width="400" height="600" src={image} alt="" />
                    )
                })}
            </div> 
        })
    }

    return(
        <div>

        <div className="telescope-titles">
            <h1>Transformandome en pandemia</h1>
            <p>
                Este ejercicio escritural, permitió que los estudiantes pudieran elaborar producciones textuales a partir de la reflexión y la comprensión de la pandemia desde su ser, así como la posibilidad de compartir sus experiencias en lo que ha sido el retorno a la presencialidad y los retos que esto ha traído.
            </p>
        </div>
                    <div className="post-container">
                    {planlector.map((post) => {
                        return(
    
                        <div className="eachPost-container">
                            <h2 className="post-name">{post.autor}</h2>
                            <img onClick={() => openPost(post)} src={post.autorFoto} alt="autor" width="250" height="300" className="post-portada"/>
                        </div>
                        )
                    })
    
                    }
                </div>
        </div>

    )
}

export default PlanLector