import React from "react";
import withReactContent from "sweetalert2-react-content";
import Swal from "sweetalert2";
import { stopmotion } from "../../data/telescopeData";

const StopMotion = () => {
    const Swaled = withReactContent(Swal)

    const openPost = (post) => {
        Swaled.fire({
            title: post.autor,
            background: '#649DC1',
            html:
            <div>
                <video height="480px" width="600px" controls src={post.video}></video>
            </div> 
        })
    }

    return(
        <div>

        <div className="telescope-titles">
            <h1>Stop Motion</h1>
            <p>
            El Stop Motion es un ejercicio de animación por medio de secuencia fotográficas en las cuales los chicos recrean una historia basada en los libros o cuentos trabajados en área de Inglés y Lengua Castellana; en su mayoría, los estudiantes realizaron los dibujos manualmente, y para su elaboración de un minuto, debieron incluir de cincuenta a noventa imagenes.            </p>
        </div>
                    <div className="post-container-stopmotion">
                    {stopmotion.map((post) => {
                        return(
    
                        <div className="eachPost-container">
                            <h2 className="post-name">{post.autor}</h2>
                            {post.autor2Foto ?
                                <div className="double-author">
                                    <img onClick={() => openPost(post)} src={post.autorFoto} alt="autor" width="250" height="300"/>
                                    <img onClick={() => openPost(post)} src={post.autor2Foto} alt="autor" width="250" height="300"/>
                                </div>
                                :
                                <img onClick={() => openPost(post)} src={post.autorFoto} alt="autor" width="250" height="300" className="post-portada"/>
                            }
                        </div>
                        )
                    })
    
                    }
                </div>
        </div>

    )
}

export default StopMotion