import React from "react";
import { moiFamille } from "../../data/telescopeData";
import withReactContent from "sweetalert2-react-content";
import Swal from "sweetalert2";

const MoiFamille = () => {

    const Swaled = withReactContent(Swal)

    const openPost = (post) => {
        Swaled.fire({
            title: post.autor,
            background: '#649DC1',
            html:
            <div>
                    {post.embed}
            </div> 
        })
    }

 return(
    <div>
        <div className="telescope-titles">
            <h1>Moi et ma Famille</h1>
            <p>
            Este es un ejercicio de escritura,  en la cual cada estudiante hace su presentación y descripción personal, dando algunos datos como nombre, gustos, edad y  otros. También hablan de su familia y animales de compañía. Paso a paso se fue desarrollando en clase escribiendo cada párrafo, para finalizar con su digitalización y decoración. 
            </p>
        </div>
        <div className="post-container">
                {moiFamille.map((post) => {
                    return(
                    <div className="eachPost-container">
                        <h2 className="post-name">{post.autor}</h2>
                        <img onClick={() => openPost(post)} src={post.autorFoto} alt="autor" width="250" height="300" className="post-portada"/>
                    </div>
                    )
                })

                }
            </div>
    </div>
 )
}

export default MoiFamille