import React from "react";
import { caligramas } from "../../data/telescopeData";
import withReactContent from "sweetalert2-react-content";
import Swal from "sweetalert2";

const Caligramas = () => {
    const Swaled = withReactContent(Swal)
    const openPost = (post) => {
        Swaled.fire({
            title: post.autor,
            background: '#649DC1',
            html:
            <div>
                <img src={post.caligrama} alt="caligrama" width="400" height="600px"/>
            </div> 
        })
    }
    return(
        <div>
            <div className="telescope-titles">
                <h1>Caligramas</h1>
                <p>
                            Los caligramas son poemas manuscritos visuales , cuyo propósito es formar una imagen alusiva al texto; para ello, los estudiantes escogieron para su elaboración temas significativos y movilizadores como la pandemia, la muerte a un ser querido, situaciones sociales, entre otros; permitiendo hacer una creación literaria con una “bella escritura”.
                </p>
            </div>
            <div className="post-container">
                {caligramas.map((post) => {
                    return(

                    <div className="eachPost-container">
                        <h2 className="post-name">{post.autor}</h2>
                        <img onClick={() => openPost(post)} src={post.autorFoto} alt="autor" width="250" height="300" className="post-portada"/>
                    </div>
                    )
                })

                }
            </div>
        </div>
    )
}

export default Caligramas